@import "./_variables.scss";

* {
  box-sizing: border-box;
}

html {
  font-size: $font-size;
  font-family: sans-serif;
}

body,
h1,
h2,
h3,
h4,
p,
ul,
ol {
  margin: 0;
  padding: 0;
}

ul,
ol {
  list-style: none;
}

a {
  color: #000000;
  text-decoration: none;
}
